.contact-row {
  margin: 200px 0;
}

.contact-form .contact-input {
  letter-spacing: .5px;
  color: #6a6a6a;
  appearance: none;
  background-color: #fff;
  border: 1px solid #ddd;
  outline: none;
  width: 100%;
  height: 46px;
  margin-bottom: 30px;
  padding-top: 3px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 14px;
  font-weight: 500;
}

input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}

textarea {
  appearance: none;
  color: #6a6a6a;
  border: 1px solid #ddd;
  outline: none;
  width: 100%;
  height: 100px;
  padding: 20px;
}

.send-msg {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .5px;
  background-color: #7744d9;
  border: none;
  border-radius: 6px;
  outline: none;
  height: 50px;
  margin-top: 20px;
  padding: 0 20px;
  font-size: 15px;
  font-weight: 500;
  line-height: 50px;
  transition: all .5s;
  display: inline-block;
}

.contact-content h5 {
  color: #7744d9;
  font-size: 15px;
}

.contact-content h2 {
  color: #1e1e1e;
  letter-spacing: .5px;
  margin-top: 10px;
  margin-bottom: 15px;
  font-size: 32px;
  font-weight: 700;
}

.contact-content p {
  color: #6a6a6a;
  font-size: 14px;
}

.contact-icon {
  text-align: center;
  color: #fff;
  background-color: #7744d9;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-right: 15px;
  font-size: 16px;
  line-height: 60px;
  display: inline-block;
}
